@charset "UTF-8";

:root {
	--logo-height: 6rem;
}

.study-wrap {
	position: relative;
	display: grid;
	grid-template-columns: 100%;
	height: 100vh;
}

#aside .aside-logo { height: var(--logo-height); background-color: #333333; }
#aside .aside-logo > a {
	display: block;
	width: 100%;
	height: inherit;
	text-align: center;
	font-size: 2rem;
	color: #ffffff;
	line-height: var(--logo-height);
}

#aside .aside-content { height: auto; padding: 1.5rem; }
#aside .aside-content .content-item ~ .content-item { margin-top: 1.5rem; }
#aside .aside-content .item-title {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding-bottom: 1rem;
	font-size: 1.7rem;
	cursor: pointer;
}

#aside .aside-content .content-item .item-title > span { transition: .35s; }
#aside .aside-content .content-item.on .item-title > span { transform: rotate(180deg); }
#aside .aside-content .content-item .item-list > li ~ li { margin-top: 1rem; }
#aside .aside-content .content-item .item-list > li > a {
	position: relative;
	display: block;
	padding-left: 2rem;
	padding-bottom: .5rem;
}

#aside .aside-content .content-item .item-list > li > a::after {
	position: absolute;
	content: '';
	left: 0;
	bottom: 0;
	width: 0;
	height: 1px;
	background-color: #155CD6;
	transition: .35s;
}

#aside .aside-content .content-item .item-list > li > a:hover { color: #155CD6; }
#aside .aside-content .content-item .item-list > li > a:hover::after { width: 100%; }

#aside .aside-content .content-item.on .item-list > li > a.on { color: #155CD6; }
#aside .aside-content .content-item.on .item-list > li > a.on::after { width: 100%; }

/* Responsive */
@media (min-width: 992px) {
	.study-wrap { grid-template-columns: 35rem auto; }
	.study-wrap #aside { box-shadow: 0 3px 6px rgba(0, 0, 0, .16); }
	#aside .aside-content { height: calc(100vh - var(--logo-height)); }
}